import Vue from 'vue'
import Router from 'vue-router'
import Top from '@/views/Top'
import Login from '@/views/Login'
import DashBoard from '@/views/DashBoard'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Top',
      component: Top
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/dashboard',
      name: 'DashBoard',
      component: DashBoard
    }
  ]
})



