<template>
  <div>
    <head-comp></head-comp>
    <!--<login-modal v-if="isShowModal"></login-modal>-->
  </div>
</template>

<script>
  import HeadComp from '@/components/HeadComp'
  import LoginModal from '@/components/LoginModal'
  import { mapActions, mapGetters } from 'vuex'

  export default {
    name: 'Top',
    components: {
      HeadComp,
      LoginModal
    },
    computed: {
      ...mapGetters({
        'isShowModal': 'getShowModal'
      })
    }
  }
</script>

<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



