<template>
  <header>
  <nav>
    <router-link to="/">トップ</router-link>
    <span @click="showModal">ログイン</span>
  </nav>
  </header>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex'

  export default {
    name: 'HeadComp',
    methods: {
      ...mapActions({
        showModal: 'showModal'
      })
    }
  }
</script>

<style scoped>
  nav{
    width: 900px;
    height: 40px;
    line-height: 40px;
    background-color: rgb(255,255,255);
    margin-left: auto;
    margin-right: auto;
    margin-top: 25px;
    position: relative;
    z-index: 2;
    text-align: right;
  }
  nav a{
    text-decoration: none;
    color: #2c3e50;
    margin-right: 10px;
  }

</style>



